import { getDataLocalStorage } from '../api/localStorageFavcCocktails';
import { FAV_INGREDIENTS } from '../utils/constans';

export const markupSearchingIngredients = arrIngred => {
  const favoriteIngredients = getDataLocalStorage(FAV_INGREDIENTS);

  return arrIngred
    .map(({ idIngredient, strIngredient, strDescription, strType }) => {
      const foundEl = favoriteIngredients.find(
        el => el.idIngredient === idIngredient
      );

      const btnAdd = `<button class="products__btn--add--ingred" data-idbtn="${idIngredient}"  data-action="${
        foundEl ? 'remove' : 'add'
      }"> ${!foundEl ? 'add to' : 'remove'}  </button>`;

      return `<li  class="products__item-Ingred" data-id="${idIngredient}" >
          <h4 >${strIngredient}</h4>
            <div  class="products__btns-Ingred"> <button  class="products__learnmore-ingred" data-name="${strIngredient}">learn more</button> ${btnAdd}</div> 
      
    </div>
        </li>`;
    })
    .join('');
};
